.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 20px;
  width: 100%;
  max-height: 400px;
  min-width: 100px;
  background-color: white;
  /* 배경화면 위로 올라오도록 z-index: 1 */
  z-index: 1;
}

.product_rating {
  display: flex;
}

.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}

.product > button {
  background-color: #61dafb;
  border: 1px solid;
  margin-top: 10px;
  /*  border-color: 상 우 하 좌 인데 좌 생략하면 우랑 같은 색 */
  border-color: #8767bf #a5a5f3 #61dafb;
}

.product_price {
  margin-top: 10px;
}

.product_info {
  height: 100px;
  margin-bottom: 15px;
}
