.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: black;
  position: sticky;
  /* sticky 박스의 부모 박스가 scroll 박스를 벗어나면 sticky 박스는 다시 일반적인 흐름에 따름 */
  top: 0;
  /* header니까 무조건 위쪽에 오게 z-index: 100 */
  z-index: 100;
}

.header_logo {
  width: 100px;
  object-fit: contain;
  /* 부모 요소 container에 현재 비율 유지한 채로 크기가 작아짐 */
  margin: 15px 20px 0px 20px;
}

.header_search {
  display: flex;
  flex: 1;
  /* flex:1 은 풀어서 보면 
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 0%;을 의미한다.
  즉, flex-basis가 0이므로 점유 크기를 0으로 만든 후 화면 비율에 따라 유연하게 늘어나거나 줄어들 수 있음을 만드는 속성이다. */
  align-items: center;
  border-radius: 20px;
}

.header_searchInput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
}

.header_searchIcon {
  background-color: #cd9042;
  padding: 5px;
  height: 15px;
}

.header_nav {
  display: flex;
  justify-content: space-evenly;
}

.header_option {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: white;
}

.header_optionLineOne {
  font-size: 10px;
}

.header_optionLineTwo {
  font-size: 13px;
  font-weight: 800;
  margin-top: 3px;
}

.header_optionBasket {
  display: flex;
  align-items: center;
  color: white;
}

.header_optionLineTwo_basketCount {
  margin-right: 10px;
  margin-left: 10px;
}
