.home {
  display: flex;
  justify-content: center;
  /* margin-left, margin-right: auto는 좌우 균형을 맞추라는 소리 */
  margin-left: auto;
  margin-right: auto;
  max-width: 1500px;
}

.home_image {
  width: 100%;
  /* 배경이미지니까 무조건 아래에 깔리게 z-index: -1 */
  z-index: -1;
  /* margin-bottom: -150px은 img아래에 깔리는 content를 img의 bottom으로부터 150px 위쪽으로 올린다는 소리 */
  margin-bottom: -150px;
  /* img에 mask 효과 */
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.home_row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
}
